.order-page__title {
  background-color: #f1f1f1;
  padding: 70px 30px 76px;
  font-size: 27px;
  line-height: 30px;
  width: auto;
  text-align: center;
  margin: 0;
}

.order-page {
  margin-bottom: 58px;
}

.order-page__title-wrapper {
  margin: -3px 0 0;
}

.order-page__mobile-wrap {
  padding: 0 30px;
}

.order-page__wrapper {
  width: 80%;
  margin: 0 auto;
  margin-bottom: 70px;
}

.order-page__text {
  font-size: 13px;
  line-height: 24px;
  color: #444444;
  margin: 0;
  padding: 24px 2px 17px 0;
}

.order-page__form {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 17px;
  line-height: 18px;
  font-weight: bold;
}

@media (min-width: 768px) {
  .order-page__text {
    font-size: 17px;
    line-height: 30px;
    padding: 5px 51px 43px 52px;
  }

  .order-page {
    margin-bottom: 54px;
  }

  .order-page__form_block {
    width: 100%;
    margin: 0;
  }

  .order-page__wrapper {
    width: 100%;
    margin: 0 auto;
  }

  .order-page__title-wrapper {
    position: relative;
    padding-top: 24px;
    padding-bottom: 18px;
    background-color: #f1f1f1;
    margin-top: -61px;
    margin-bottom: 53px;
    z-index: -10;
  }

  .order-page__title {
    width: auto;
    font-size: 43px;
    line-height: 50px;
  }

  .order-page__title::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 20px;
    left: 0;
    bottom: 0;
    background-image: url("../img/bg-zigzag-fill.svg");
  }
}

@media (min-width: 1150px) {
  .order-page__text {
    width: 700px;
    margin: 0 auto;
    padding: 18px 0 0;
  }

  .order-page__form_block {
    width: 100%;
  }

  .order-page__title-wrapper {
    margin-top: -58px;
    padding-top: 173px;
    padding-bottom: 82px;
    margin-bottom: 102px;
  }

  .order-page__title {
    padding: 0;
    width: 100%;
    font-size: 53px;
    line-height: 57px;
    margin-top: -80px;
  }

  .order-page__wrapper {
    width: 1050px;
    margin: 0 auto;
    margin-bottom: 107px;
  }
}

@media (min-width: 768px) {
  .order-page__mobile-wrap {
    width: 100%;
    margin: 0;
    padding: 0;
    background-color: #ffffff;
  }
}
